import React from 'react'

const OrderCom = ({ item }) => {
    return (
        <>
            <div className="container mx-auto px-4">
                <div className="flex flex-col md:flex-row gap-4">
                    <div className='w-full'>
                        <div className="bg-white rounded-lg shadow-md p-6 mb-4">
                            <span className="font-semibold">{item.product.title}</span>
                            <table className="w-full">
                                <thead>
                                    <tr>
                                        <th className="text-left font-semibold">Product</th>
                                        <th className="text-left font-semibold">Price</th>
                                        <th className="text-left font-semibold">Quantity</th>
                                        <th className="text-left font-semibold">Total</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td className="py-4">
                                            <div className="flex items-center">
                                                <img className="h-16 w-16 mr-4" src={item.product.image} alt="Product" />
                                            </div>
                                        </td>
                                        <td className="py-4">{item.product.price}</td>
                                        <td className="py-4">
                                            <div className="flex items-center">
                                                <span className="text-center w-8">{item.count}</span>
                                            </div>
                                        </td>
                                        <td className="py-4">{item.product.price * item.count}</td>
                                    </tr>

                                </tbody>
                            </table>
                            <div className='flex justify-between'>
                                <div>
                                    <span className='text-gray-700 font-semibold'>order date: </span>
                                    <span className='text-pink-800 text-sm'>{item.date}</span>
                                </div>
                                <div>
                                    <span className='text-gray-700 font-semibold'>Expected delivery withhin 5 day</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default OrderCom
