import React, { createContext, useEffect, useState } from 'react';
export const UserContxt = createContext();

const UserContext = ({ children }) => {
    const [User, setUser] = useState([]);

    useEffect(() => {
        const getUser = async () => {
            const res = await fetch('/user/userDetails', {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json"
                },
                credentials: "include",
            })
            const data = await res.json();
            setUser([...data]);
        }
        getUser();
    }, []);

    const updateProfile = async (info) => {
        const { name, email, pin, place } = info;

        await fetch("/user/updateProfile", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ name, email, pin, place })
        });
    }


    return (
        <UserContxt.Provider value={{ User, updateProfile }}>
            {children}
        </UserContxt.Provider>
    )
}

export default UserContext;
