import React from 'react'
import { Rating } from "@material-tailwind/react";
import { useNavigate } from 'react-router-dom';



const Card = ({ item }) => {
    const navigate = useNavigate();
    return (
        <div onClick={() => navigate(`/product/${item._id}`)} className='border border-gray-400 mb-4 p-3'>
            <div className='h-[200px] group transition'>
                <div className='w-full h-full flex justify-center items-center'>
                    <div className='w-[200px] mx-auto flex justify-center items-center cursor-pointer'>
                        <img src={item.image} alt='_blank' className='max-h-[160px] group-hover:scale-110 transition duration-300 z-[-1]' />
                    </div>
                </div>
            </div>
            <div className='mx-2'>
                <p className='truncate cursor-pointer hover:text-blue-600'>{item.description}</p>
                <Rating value={4} readonly />;
                <p className='truncate font-medium text-gray-600'>$ {item.price}</p>
            </div>
        </div>

    )
}

export default Card;
