import React, { useState, useContext } from 'react';
import {
    Button,
    Dialog,
    Card,
    CardBody,
    CardFooter,
    Typography,
    Input
} from "@material-tailwind/react";
import { useNavigate } from "react-router-dom";

import { OrderContxt } from '../Context/OrderContext';


const OrderModal = ({ User, Item, count, open, setOpen }) => {
    const navigate = useNavigate();
    const { addToOrder } = useContext(OrderContxt);

    const [info, setInfo] = useState({
        email: User[0].email,
        pin: User[0].pin,
        place: User[0].place
    });

    const UploadData = (e) => {
        const { name, value } = e.target;
        setInfo({ ...info, [name]: value });
    }

    const submit = async (e) => {
        e.preventDefault();
        const { email, pin, place } = info;

        if (email || pin || place) {
            addToOrder(Item, count, info);
            setOpen(!open);
            navigate("/");
        }
    }

    return (
        <>
            <Dialog
                size="xs"
                open={open}
                handler={() => setOpen(!open)}
                className="bg-transparent shadow-none"
            >
                <Card className="mx-auto w-full max-w-[24rem]">
                    <CardBody className="flex flex-col gap-4">
                        <Typography variant="h4" color="blue-gray">
                            For Take Order
                        </Typography>
                        <Typography
                            className="mb-3 font-normal"
                            variant="paragraph"
                            color="gray"
                        >
                            Enter your Email and pin and place.
                        </Typography>
                        <Typography className="-mb-2" variant="h6">
                            Your Email
                        </Typography>
                        <Input label="Email" size="lg" type='email' name='email'
                            value={info.email} onChange={UploadData} required />
                        <Typography className="-mb-2" variant="h6">
                            Your pin
                        </Typography>
                        <Input label="pin" size="lg" type='text' name='pin'
                            value={info.pin} onChange={UploadData} required />
                        <Typography className="-mb-2" variant="h6">
                            Your place
                        </Typography>
                        <Input label="place" size="lg" type='text' name='place'
                            value={info.place} onChange={UploadData} required />
                    </CardBody>
                    <CardFooter className="pt-0">
                        <Button onClick={submit} variant="gradient" fullWidth>
                            Sign In
                        </Button>
                    </CardFooter>
                </Card>
            </Dialog>

        </>
    )
}

export default OrderModal;
