import React from "react";
import {
    Button,
    Dialog,
    DialogHeader,
    DialogFooter,
} from "@material-tailwind/react";

import { NavLink } from "react-router-dom";

const LoginShow = ({ handleOpen, open }) => {

    return (
        <>
            <Dialog
                open={open}
                handler={handleOpen}
                animate={{
                    mount: { scale: 1, y: 0 },
                    unmount: { scale: 0.9, y: -100 },
                }}
            >
                <DialogHeader>Login now !</DialogHeader>
                <DialogFooter>
                    <Button
                        variant="text"
                        color="red"
                        onClick={handleOpen}
                        className="mr-1"
                    >
                        <span>Cancel</span>
                    </Button>
                    <NavLink to="/signin">
                        <Button variant="gradient" color="green" >
                            <span>login</span>
                        </Button>
                    </NavLink>
                </DialogFooter>
            </Dialog>
        </>
    );
}


export default LoginShow;