import React, { createContext, useEffect, useState } from 'react';
export const CartContxt = createContext();

const CartContext = ({ children }) => {
    const [cart, setCart] = useState([]);

    const [total, setTotal] = useState(0);

    const [totalItem, setTotalItem] = useState(0);

    useEffect(() => {
        const getData = async () => {
            try {
                const res = await fetch("/user_carts/getCarts", {
                    method: "GET",
                    headers: {
                        Accept: "application/json",
                        "Content-Type": "application/json"
                    },
                    credentials: "include",
                });
                const data = await res.json();
                setCart(data);
            } catch (e) {
                console.log(e);
            }
        }
        getData();
    }, []);

    useEffect(() => {
        const amount = cart.reduce((accumulator, currentItem) => {
            return accumulator + currentItem.product.price * currentItem.amount;
        }, 0);

        setTotal(amount);

        const numberItem = cart.reduce((accumulator, curremtItem) => {
            return accumulator + curremtItem.amount;
        }, 0)

        setTotalItem(numberItem);

    }, [cart]);



    const addToCart = async (Item, count) => {
        const newitem = { product: Item, amount: count };

        const cartItem = cart.find((item) => {
            return item.product._id === Item._id;
        });

        if (cartItem) {
            const itm = cart.map((item) => {
                if (item.product._id === Item._id) {
                    return { ...item, amount: item.amount + count };
                }
                else {
                    return item;
                }
            });
            setCart(itm);
        }
        else {
            setCart([...cart, newitem]);
        }

        await fetch(`/user_carts/addTocart/${Item._id}/${count}`, {
            method: "GET",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json"
            },
            credentials: "include"
        });

    }

    const removecart = async (_id) => {
        try {
            const itm = cart.filter((item) => {
                return item.product._id !== _id;
            });

            setCart(itm);

            await fetch(`/user_carts/deletecart/${_id}`, {
                method: "GET",
                headers: {
                    Accept: "application/json",
                    "Content-Type": "application/json"
                },
                credentials: "include",
            });
        }
        catch (e) {
            console.log(e);
        }
    }


    return (
        <CartContxt.Provider value={{
            addToCart, removecart, total, totalItem, cart
        }}>
            {children}
        </CartContxt.Provider>
    )
}

export default CartContext;
