import React, { useContext } from "react";
import {
    Navbar,
    Collapse,
    Typography,
    IconButton,
} from "@material-tailwind/react";
import { Bars3Icon, XMarkIcon } from "@heroicons/react/24/outline";
import { NavLink } from "react-router-dom";
import Avatar from "./Avatar";
import { CartContxt } from "../Context/CartContext";

function NavList() {
    const { totalItem } = useContext(CartContxt);
    return (
        <ul className="my-2 flex flex-col gap-2 lg:mb-0 lg:mt-0 lg:flex-row lg:items-center lg:gap-6">
            <Typography
                as="li"
                variant="small"
                color="blue-gray"
                className="p-1 font-medium flex items-center"
            >
                <NavLink to="/signin" className={({ isActive }) => (isActive ? "text-blue-500" : "hover:text-blue-500 transition-colors")} >
                    login
                </NavLink>
            </Typography>
            <Typography
                as="li"
                variant="small"
                color="blue-gray"
                className="p-1 font-medium"
            >
                <div className="flex items-center hover:text-blue-500 transition-colors">
                    <Avatar />
                </div>
            </Typography>

            <Typography
                as="li"
                variant="small"
                color="blue-gray"
                className="p-1 font-medium cursor-pointer"
            >
                <NavLink to='/user/cartList' className={({ isActive }) => (isActive ? "text-blue-500" : "hover:text-blue-500 transition-colors")}>
                    <div className="flex items-center hover:text-blue-500 transition-colors text-2xl">
                        <ion-icon name="cart-outline"></ion-icon>
                    </div>
                    <div className='text-md relative top-[-32px] right-[-15px] lg:absolute lg:top-[19px] lg:right-[20px] bg-red-600 w-[18px] h-[18px]
                p-[2px] rounded-full text-white flex justify-center items-center'>{totalItem}</div>
                </NavLink>

            </Typography>
        </ul>
    );
}

export default function NavbarSimple() {
    const [openNav, setOpenNav] = React.useState(false);

    const handleWindowResize = () =>
        window.innerWidth >= 960 && setOpenNav(false);

    React.useEffect(() => {
        window.addEventListener("resize", handleWindowResize);

        return () => {
            window.removeEventListener("resize", handleWindowResize);
        };
    }, []);

    return (
        <Navbar className="mx-auto max-w-screen-xl px-6 py-3">
            <div className="flex items-center justify-between text-blue-gray-900">
                <Typography
                    as="a"
                    variant="h6"
                    className="mr-4 cursor-pointer py-1.5"
                >
                    <NavLink to='/'>Delivery.in</NavLink>
                </Typography>
                <div className="hidden lg:block">
                    <NavList />
                </div>
                <IconButton
                    variant="text"
                    className="ml-auto h-6 w-6 text-inherit hover:bg-transparent focus:bg-transparent active:bg-transparent lg:hidden"
                    ripple={false}
                    onClick={() => setOpenNav(!openNav)}
                >
                    {openNav ? (
                        <XMarkIcon className="h-6 w-6" strokeWidth={2} />
                    ) : (
                        <Bars3Icon className="h-6 w-6" strokeWidth={2} />
                    )}
                </IconButton>
            </div>
            <Collapse open={openNav}>
                <NavList />
            </Collapse>
        </Navbar>
    );
}