import React, { useState } from 'react'
import { Input, Button } from "@material-tailwind/react";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Navbar from "../Component/Navbar";


const SignUp = () => {
  const [Info, setInfo] = useState({
    phone: "",
    password: ""
  });

  const UploadData = (e) => {
    const { name, value } = e.target;
    setInfo({ ...Info, [name]: value });
  }

  const submit = async (e) => {
    try {
      e.preventDefault();
      const { phone, password } = Info;
      const res = await fetch("/user/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({ phone, password })
      });

      if (res.status === 301) {
        window.location.assign("/").reload();
      }
      else {
        const data = await res.json();
        toast.error(`${data.msg}`, {
          position: "bottom-left"
        });
      }
    }
    catch (e) {
      console.log(e);
    }
  }
  return (
    <>
      <Navbar />

      <div className='w-screen mt-16 flex justify-center items-center'>
        <div className='md:border md:border-gray-400 md:w-[450px] md:h-[501px]
      flex justify-center items-center rounded'>
          <div>
            <div className='text-3xl text-center mb-10'>
              <div className='w-[50px] h-[50px] border border-blue-gray-900 mx-auto 
            flex justify-center items-center rounded-full'>
                <ion-icon name="person-outline"></ion-icon>
              </div>
            </div>
            <div className="flex w-72 flex-col gap-6">
              <Input size="lg" color="blue" label="Enter Phone"
                name='phone' type='text' value={Info.phone} onChange={UploadData} required />
              <Input size="lg" color="blue" label="Enter Password"
                name='password' type='password' value={Info.password} onChange={UploadData} required />
            </div>

            <Button type='submit' onClick={submit} className="mt-6" fullWidth>
              sign in
            </Button>

          </div>
        </div>
      </div>
      <ToastContainer theme="colored" />
    </>
  )
}

export default SignUp;
