import React, { useContext, useState } from 'react';
import { useParams } from 'react-router-dom';

// assign Product in page
import ProductCom from '../SubComponent/Product';

// assign context
import { ProductContxt } from "../Context/ProductContext";


import { UserContxt } from "../Context/UserContext";

// assign modal
import OrderModal from '../SubComponent/OrderPlace';
import OrderLoginModal from '../SubComponent/OrderLoginShow';

const ProductDetails = () => {

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen((cur) => !cur);
  const { User } = useContext(UserContxt);

  const [count, setCount] = useState(1);
  const { i } = useParams();

  const { product } = useContext(ProductContxt);

  const Item = product.find((item) => {
    return item._id === i;
  });

  if (!Item) {
    return <></>
  }


  const UpdateIncrease = () => {
    if (count < Item.rating.count) {
      setCount(count + 1);
    }
  };
  const UpdateDecrease = () => {
    if (count > 1) {
      setCount(count - 1);
    }
  };

  return (
    <>
      <ProductCom Item={Item} handleOpen={handleOpen} count={count}
        UpdateIncrease={UpdateIncrease} UpdateDecrease={UpdateDecrease} />

      {User.length === 1 ? <OrderModal User={User} Item={Item} count={count} open={open}
        setOpen={setOpen} /> :
        <OrderLoginModal open={open} handleOpen={handleOpen} />}
    </>
  )
}

export default ProductDetails;
