import React, { useContext } from 'react';
import Navbar from "../Component/Navbar";
import { OrderContxt } from "../Context/OrderContext";
import OrderItem from '../SubComponent/OrderItem';

const Order = () => {
    const { order } = useContext(OrderContxt);

    if (!order) {
        return <></>
    }

    return (
        <>
            <Navbar />
            <div className="bg-gray-100 h-screen py-8 mt-7">
                {
                    order.map((item) => {
                        return <OrderItem key={item._id} item={item} />
                    })
                }
            </div>
        </>
    )
}

export default Order;
