import React from 'react';
import { useNavigate } from "react-router-dom";

const CartCom = ({ item, removecart }) => {
    const navigate = useNavigate();
    return (
        <>
            <div className="justify-between mb-6 rounded-lg bg-white p-6 shadow-md sm:flex sm:justify-start">
                <img src={item.product.image} alt="product" className="w-full rounded-lg sm:w-40" />
                <div className="sm:ml-4 sm:flex sm:w-full sm:justify-between">
                    <div className="mt-5 sm:mt-0">
                        <h2 onClick={() => navigate(`/product/${item.product._id}`)} className="text-lg font-bold text-gray-900 hover:text-gray-600 cursor-pointer">{item.product.category}</h2>
                        <p className="mt-1 text-xs text-gray-700">Total: {item.product.rating.count}</p>
                    </div>
                    <div className="mt-4 flex justify-between sm:space-y-6 sm:mt-0 sm:block sm:space-x-6">
                        <div className="flex items-center border-gray-100">
                            <span className='py-1 px-3.5 text-md text-gray-700'>Total carts: </span>
                            <span className="h-8 text-xs text-gray-700 text-center flex items-center" >
                                {item.amount}</span>
                        </div>
                        <div className="flex items-center space-x-4">
                            <p className="text-sm">$ {item.product.price}</p>
                            <span onClick={() => removecart(item.product._id)}
                                className='text-lg text-red-600 flex justify-center items-center cursor-pointer'>
                                <ion-icon name="trash-outline"></ion-icon>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </>

    )
}

export default CartCom
