import React, { useContext, useState } from 'react'
import Navbar from "../Component/Navbar";

import { UserContxt } from "../Context/UserContext";
import UserProfile from '../SubComponent/User';
import OrderLoginShow from "../SubComponent/OrderLoginShow";

const UserDetails = () => {
    const { User } = useContext(UserContxt);
    const [open, setOpen] = useState(true);

    if (!User) {
        return;
    }

    const handleOpen = () => setOpen(!open);

    return (
        <>
            <Navbar />
            {
                User.length === 0 ? <OrderLoginShow handleOpen={handleOpen} open={open} />
                    : <UserProfile user={User} />

            }

        </>
    )
}

export default UserDetails;
