import React, { createContext, useEffect, useState } from 'react';
export const OrderContxt = createContext();

const OrderContext = ({ children }) => {
    const [order, setOrder] = useState([]);

    useEffect(() => {
        const getOrders = async () => {
            try {
                const res = await fetch("/user_orders/getAllOrders", {
                    method: "GET",
                    headers: {
                        Accept: "application/json",
                        "Content-Type": "application/json"
                    },
                    credentials: "include",
                });
                const data = await res.json();
                setOrder(data);

            } catch (e) {
                console.log(e);
            }
        }
        getOrders();
    }, []);

    const addToOrder = async (Item, count, info) => {
        const newOrder = { product: Item, count: count, date: new Date().toLocaleDateString() };
        setOrder([...order, newOrder]);

        const { email, pin, place } = info;


        await fetch(`/user_orders/addToOrder/${Item._id}/${count}/${email}/${pin}/${place}`, {
            method: "GET",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json"
            },
            credentials: "include",
        });
    }

    return (
        <OrderContxt.Provider value={{ order, addToOrder }}>
            {children}
        </OrderContxt.Provider>
    )
}

export default OrderContext;
