import React, { useContext } from 'react'
import { CartContxt } from "../Context/CartContext";
import Navbar from "../Component/Navbar";
import CartCom from './CartCom';

const Cart = () => {
  const { cart, total, removecart } = useContext(CartContxt);
  if (!cart) {
    return <>loading ...... </>
  }

  return (
    <>
      <Navbar />
      <div className="h-scree pt-20 mt-5">
        <h1 className="mb-10 text-center text-2xl font-bold">Cart Items</h1>
        <div className="mx-auto max-w-5xl justify-center px-6 md:flex md:space-x-6 xl:px-0">
          <div className="rounded-lg md:w-2/3">
            {
              cart.map((item) => {
                return <CartCom key={item._id} item={item} removecart={removecart} />
              })
            }
          </div>

          <div className="mt-6 h-full rounded-lg border bg-white p-6 shadow-md md:mt-0 md:w-1/3">
            <div className="mb-2 flex justify-between">
              <p className="text-gray-700">Subtotal</p>
              <p className="text-gray-700">${total}</p>
            </div>
            <div className="flex justify-between">
              <p className="text-gray-700">GST</p>
              <p className="text-gray-700">$4.99</p>
            </div>
            <hr className="my-4" />
            <div className="flex justify-between">
              <p className="text-lg font-bold">Total</p>
              <div className="">
                <p className="mb-1 text-lg font-bold">${total + 4.99} USD</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Cart;
