import React, { useState, useContext } from 'react';
import validator from 'validator';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Button } from "@material-tailwind/react";
import { UserContxt } from '../Context/UserContext';

const User = ({ user }) => {
    const { updateProfile } = useContext(UserContxt);

    const { name, email, phone, pin, place } = user[0];

    const [userInfo, setUserInfo] = useState({
        name: name,
        email: email,
        phone: phone,
        pin: pin,
        place: place
    });

    const Update = (e) => {
        const { name, value } = e.target;
        setUserInfo({ ...userInfo, [name]: value });
    }

    const submit = (e) => {
        e.preventDefault();
        if (userInfo.pin >= 10000 && userInfo.pin < 1000000 && validator.isEmail(userInfo.email)) {
            updateProfile(userInfo);
        }
        else {
            toast.error(`invalid Email and Pin code`, {
                position: "bottom-left"
            });
        }
    }


    return (
        <>
            <div className='h-screen py-10 bg-[#071e34] flex justify-center items-center'>
                <div className="bg-white max-w-2xl shadow overflow-hidden sm:rounded-lg px-6">
                    <div className="px-4 py-5 sm:px-6">
                        <div className="mt-6 w-fit mx-auto text-4xl text-fuchsia-600">
                            <ion-icon name="person-add"></ion-icon>

                        </div>
                        <p className="mt-1 max-w-2xl text-sm text-gray-500 text-center">
                            Details and informations about user.
                        </p>
                    </div>
                    <div className="border-t border-gray-200">
                        <dl>
                            <div className="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                                <dt className="text-md font-medium text-gray-500">
                                    Name
                                </dt>
                                <dd className='mt-1 text-gray-900 sm:mt-0 sm:col-span-2'>
                                    <input name='name' type='text' className="text-md outline-none bg-transparent border-b border-gray-800 capitalize"
                                        value={userInfo.name} onChange={Update} />
                                </dd>
                            </div>

                            <div className="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                                <dt className="text-md font-medium text-gray-500">
                                    Email
                                </dt>
                                <dd className='mt-1 text-gray-900 sm:mt-0 sm:col-span-2'>
                                    <input name='email' type='email' className="text-md outline-none bg-transparent border-b border-gray-800"
                                        value={userInfo.email} onChange={Update} />
                                </dd>

                            </div>
                            <div className="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                                <dt className="text-md font-medium text-gray-500">
                                    Phone
                                </dt>
                                <dd className='mt-1 text-gray-900 sm:mt-0 sm:col-span-2'>
                                    <input name='phone' type='text' className="text-md outline-none bg-transparent border-b border-gray-800"
                                        value={userInfo.phone} />
                                </dd>

                            </div>
                            <div className="bg-white px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                                <dt className="text-md font-medium text-gray-500">
                                    Pin code
                                </dt>
                                <dd className='mt-1 text-gray-900 sm:mt-0 sm:col-span-2'>
                                    <input name='pin' type='number' className="text-md outline-none bg-transparent border-b border-gray-800"
                                        value={userInfo.pin} onChange={Update} />
                                </dd>

                            </div>
                            <div className="bg-gray-50 px-4 py-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-6">
                                <dt className="text-md font-medium text-gray-500">
                                    place
                                </dt>
                                <dd className='mt-1 text-gray-900 sm:mt-0 sm:col-span-2'>
                                    <input name='place' type='text' className="text-md outline-none bg-transparent border-b border-gray-800 capitalize"
                                        value={userInfo.place} onChange={Update} />
                                </dd>
                            </div>
                            <div className='text-left py-5 ml-4'
                                onClick={submit}>
                                <Button variant="text" className='text-cyan-300'>update</Button>
                            </div>
                        </dl>
                    </div>
                </div>
            </div>
            <ToastContainer theme="colored" />
        </>
    )
}

export default User;
